.HeroContainer{
  height: 60vh;
  display: flex;
  cursor: pointer;
  margin-bottom: 30px;
}

.SmallContainer{
 margin-left: 2.5px;
  /* margin-right: 5px; */
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: .2em;
  width: 50%;
}

.BigHero{
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  text-align: center;
  overflow: hidden;
  width: 50%;
  height: 60vh;
}

.SmallItem{
  
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  align-self: center;
  text-align: center;
  overflow: hidden;
  width: 100%;
  height: 100%;  
}

.back{
  width: 100%;
  height: 100%;
  background: linear-gradient(#424ba400,#000000a6);
  position: absolute;
}
.pack,.pack2{
  text-align: center;
  width: 100%;
  top: 75%;
  position: absolute;
}
.pack2{
  top: 60%;
  padding: 2%;
  
}
.postTag{
  display: inline-block;
  padding: 5px;
  background: #73b3df;
  
  color: #fff;
  font-family: "Oswald",sans-serif;
  letter-spacing: .6px;
  font-weight: 400!important;
  list-style: none;
  font-size: .75rem;
  text-transform: uppercase;
  text-align: center;
}

.postTag:before {
  content: '\f2c0';
  font-family: 'FontAwesome';
  margin-right: 4px;
}
.pack h2 , .pack2 h2{
  font-size: 1.5rem;
  font-weight: 600;
  display: block;
  line-height: 1.5em;
  margin: 5px 0 5px;
  color: #fff;
  background-color: transparent;
}
.pack2 h2{
font-size: 1.1rem;
}

.postTitle a{
  color: #fff;
  display: block;
  text-shadow: 1px 1px 3px rgba(0,0,0,0.2);
  font-family: 'Oswald', sans-serif;
 
  font-weight: 400;
  line-height: 1.2;
}

